import React from 'react'
import { NavLink } from 'react-router-dom';
import { mostrarImagen } from '../../helpers/user';

export const Topbar = () => {

    //Menu toggle
    const toggleFunction = () => {
    
        let toggle = document.querySelector('.toggle');
        let main = document.querySelector('.main');
        let navigation = document.querySelector('.navigationUser');

        navigation.classList.toggle('active');
        main.classList.toggle('active');        
    }

    //User-img
    const user = JSON.parse(localStorage.getItem('user') || {logged: false});

    mostrarImagen(user.uid);

  return (
        <div className='row'>
            <div className='col-lg-12'>
                <div className='main'>
                <div className='topbar'>
                    <div className='toggle' onClick={toggleFunction}>
                        <ion-icon name="menu-outline"></ion-icon> 
                    </div>

                    <div className='search'>
                        <label>
                            <input type='text' placeholder='Buscar documento...' />
                            <ion-icon name="search-outline"></ion-icon>
                        </label>
                    </div>

                    <div className='user'>
                        <NavLink 
                            to='/user/config'
                        >
                            <img src={user.img} />
                        </NavLink>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}
